.link {
  color: #d9f2f4;
  text-decoration: none;
}
.link.brackets::before {
  content: "[ ";
}
.link.brackets::after {
  content: " ]";
}
.link:hover, .link:active, .link:focus {
  -webkit-animation: BtnClick 2s linear;
  -moz-animation: BtnClick 2s linear;
  animation: BtnClick 2s linear;
}
@-webkit-keyframes BtnClick {
  0% {
    background: #d9f2f4;
    color: black;
  }
  25% {
    background: transparent;
    color: #d9f2f4;
  }
  50% {
    background: #d9f2f4;
    color: black;
  }
  75% {
    background: transparent;
    color: #d9f2f4;
  }
  100% {
    background: #d9f2f4;
    color: black;
  }
}
@-moz-keyframes BtnClick {
  0% {
    background: #d9f2f4;
    color: black;
  }
  25% {
    background: transparent;
    color: #d9f2f4;
  }
  50% {
    background: #d9f2f4;
    color: black;
  }
  75% {
    background: transparent;
    color: #d9f2f4;
  }
  100% {
    background: #d9f2f4;
    color: black;
  }
}
@keyframes BtnClick {
  0% {
    background: #d9f2f4;
    color: black;
  }
