  }
  100% {
    background: #d9f2f4;
    color: black;
  }
}

.btn {
  margin: 5px;
  color: black;
  background: #93EDF5;
  text-decoration: none;
  text-transform: uppercase;
  text-shadow: none;
  font-weight: bold;
  border: 2px solid #93EDF5;
  -webkit-box-shadow: 0px 0px 4px 2px rgba(2, 109, 29, 0.78);
  -moz-box-shadow: 0px 0px 4px 2px rgba(1, 104, 32, 0.78);
  box-shadow: 0px 0px 4px 2px rgba(18, 184, 101, 0.78);
}
.btn:hover, .btn:focus, .btn:active {
  background: transparent;
  color: #93EDF5;
  text-shadow: 0px 0px 5px rgba(0, 255, 255, 0.945);
}
.btn.clicked {
  -webkit-animation: BtnClick 1s linear;
